// Dependencies
import Vue from 'vue'
import App from './App.vue'
import router from './config/router'
import store from './data/store/store'

// Vue settings
Vue.config.productionTip = false

// Global filters
Vue.filter('time', value => {
  return value.toLocaleString() + 'hrs'
})

// Vue instance
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
