// Simulated data
import skills from '../../info/skills'

// Store module
export default {
  state: {
    languages: [],
    frameworks: [],
    databases: [],
    multimedia: []
  },
  mutations: {
    setSkills (state, skills) {
      state.languages = skills.languages
      state.frameworks = skills.frameworks
      state.databases = skills.databases
      state.multimedia = skills.multimedia
    }
  },
  actions: {
    initSkills ({ commit }) {
      commit('setSkills', skills)
    }
  },
  getters: {
    languages (state) {
      return state.languages
    },
    frameworks (state) {
      return state.frameworks
    },
    databases (state) {
      return state.databases
    },
    multimedia (state) {
      return state.multimedia
    }
  }
}
