// Simulated data
import carrers from '../../info/carrers'

// Store module
export default {
  state: {
    jobs: []
  },
  mutations: {
    setJobs (state, carrers) {
      state.jobs = carrers
    }
  },
  actions: {
    initJobs ({ commit }) {
      commit('setJobs', carrers)
    }
  },
  getters: {
    jobs (state) {
      return state.jobs
    }
  }
}
