// Dependencies
import Vue from 'vue'
import Vuex from 'vuex'

// Modules
import certifications from './modules/certifications'
import carrers from './modules/carrers'
import graduations from './modules/graduations'
import skills from './modules/skills'

// Vue uses
Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    certifications,
    carrers,
    graduations,
    skills
  }
})
