<template>
  <div id="app">
    <Navigation/>
    <div class="app-container">
      <transition name="slide" mode="out-in">
        <router-view/>
      </transition>
    </div>
  </div>
</template>

<script>
import Navigation from './ui/components/Navigation'
export default {
  components: { Navigation },
  created () {
    this.$store.dispatch('initCourses')
    this.$store.dispatch('initSkills')
    this.$store.dispatch('initJobs')
    this.$store.dispatch('initGraduations')
  }
}
</script>

<style>
/* Global styles */
* {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  user-select: none;
}
/* Desktops breakpoint */
@media (min-width: 992px) {
  body {
    padding: 2rem 2rem 2rem 17rem;
    background-color: #f5f5f5;
    min-height: 97vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .app-container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
  .page-container {
    max-width: 81.63rem; /* 1306px on a 16px base */
    margin: 0 auto;
  }
  .page-title {
    font-size: 2rem;
    text-transform: uppercase;
    text-align: center;
    color: #212121;
    margin: 0 auto 2rem auto;
  }
  /* Animation styles */
  .slide-enter-active {
    animation: slide-in 0.3s ease;
  }
  .slide-leave-active {
    animation: slide-out 0.3s ease;
  }
  @keyframes slide-in {
    from {
      transform: translateY(-30px);
      opacity: 0;
    }
    to {
      transform: translateY(0px);
      opacity: 1;
    }
  }
  @keyframes slide-out {
    from {
      transform: translateY(0);
      opacity: 1;
    }
    to {
      transform: translateY(-30px);
      opacity: 0;
    }
  }
}
/* Mobiles breakpoint */
@media (max-width: 991px) {
  body {
    max-width: 100%;
    margin-top: 5rem;
    background: #f5f5f5;
  }
  .app-container {
    max-width: 100%;
  }
  .page-container {
    max-width: 100%;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .page-title {
    font-size: 1.5rem;
    text-transform: uppercase;
    text-align: center;
    color: #212121;
    margin: 0 auto 1rem auto;
  }
  .sub-title {
    font-size: 1rem;
    font-weight: bold;
    color: #212121;
    font-weight: 300;
    text-align: center;
  }
}
</style>
