// Simulated data
import courses from '../../info/certifications'

// Store module
export default {
  state: {
    courses: []
  },
  mutations: {
    setCourses (state, courses) {
      state.courses = courses
    }
  },
  actions: {
    initCourses ({ commit }) {
      commit('setCourses', courses)
    }
  },
  getters: {
    courses (state) {
      return state.courses
    }
  }
}
