// Dependencies
import Vue from 'vue'
import Router from 'vue-router'

// Components imports
import About from '@/ui/views/About.vue'

// Vue use
Vue.use(Router)

// Router config
export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: About
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/carrers',
      name: 'carrers',
      component: () => import(/* lazy loaded */ '@/ui/views/Carrers.vue')
    },
    {
      path: '/graduations',
      name: 'graduations',
      component: () => import(/* lazy loaded */ '@/ui/views/Graduations.vue')
    },
    {
      path: '/skills',
      name: 'skills',
      component: () => import(/* lazy loaded */ '@/ui/views/Skills.vue')
    },
    {
      path: '/interests',
      name: 'interests',
      component: () => import(/* lazy loaded */ '@/ui/views/Interests.vue')
    },
    {
      path: '/certifications',
      name: 'certifications',
      component: () => import(/* lazy loaded */ '@/ui/views/Certifications.vue')
    }
  ]
})
