// Simulated data
import graduations from '../../info/graduations'

// Store module
export default {
  state: {
    graduations: []
  },
  mutations: {
    setGraduations (state, graduations) {
      state.graduations = graduations
    }
  },
  actions: {
    initGraduations ({ commit }) {
      commit('setGraduations', graduations)
    }
  },
  getters: {
    graduations (state) {
      return state.graduations
    }
  }
}
